.header{

    .mobile{
        display:none;
    }
    width: 232px;
    background: #222629;
    height: 100%;
    position: fixed;
    color:white;
    overflow: scroll;
    /* IE scroll 숨김 */
    -ms-overflow-style: none;
    overflow-y:scroll;
    overflow-y:hidden;
    overflow-x:scroll ;
    overflow-x: hidden;
    a{
        color:white;
        text-decoration: none;;
    }
    
    .Logo{
        height: 100px;
        display:flex;
        margin-top:30px;
        .Logo_avatar img{
           width:48px;
           height:48px;
        }
        .text{
            margin-left:16px;
            text-align: center;
            margin-top:12px;
        }
    }
    .menu{
        svg{
            margin-right:125px;
            
        }
        li{
            height: 50px;
            font-size:21px;
        }
    }
    .sns ul{
        display:flex;
        margin-top:20rem;
        li{
            width: 40px;
            margin:0 auto;
        }
    }
}

@media screen and  (max-width:767px){
    .header{
        background:none;
        .Logo{
            display:none;
        }
        .menu{
            display:none;
        }
        .mobile{
            display:block;
            color:white;
            svg{
                font-size:46px;
            }
        }
    
        .mobile{
            .mobile_Logo{

                background-color: rgba(34, 38, 41, 0.8);
                justify-content: space-between;
                text-align: center;
                
                .mobile1{
                    height: auto;
                    display:flex;
                    justify-content: space-between;
                    margin-bottom:50px;
                }
                .mobile_avatar img{
                    height: 60px;
                    width: 60px;
                }
                .mobile_text{
                    margin-top:14px;
                }
                .mobile_menu{
                    .mobile_Nav{
                        ul li{
                            display:flex;  
                            justify-content: space-between;
                            margin-bottom:20px;
                            height: 50px;
                        }
                        svg{
                            margin-right:120px;
                        }
                    }
                    .mobile_sns ul{
                        display:flex;
                        margin-top:13rem;
                        li{
                            width: 50px;
                            margin:0 auto;
                        }
                    }
                }
            }
            
            
        }
    }
}