main{
    a{
        color:blue;
        text-decoration:none;
    }
    width: 100%;
    .title{
        font-weight: 700;
        padding-top:50px;
        text-align: center;
        font-size:48px;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    }


    /* ----Intro----- */
    .Intro{
        height: 100%; width: 100%;
        background-image: url(../img/banner.png);
        .Intro_div{
            margin-left:232px;
            text-align: center;
            padding:100px;
            color:white;
        } 
        .Intro_header{
            font-size:50px;
            margin-bottom:40px;
            
        }
        p{
            font-size:14px;
            line-height: 30px;
        }
    }

    /* --- About --- */

    .About{
        margin-left:232px;
        background-color: #f5fafa;
        padding-bottom:80px;
       
        .About_information{
            margin:0 auto;
            display:grid;
            grid-template-columns: repeat(3, 400px);
            svg{
                font-size:48px;
                margin-right:13px;
            }
            .About_informations{
                display:flex;
                width: 240px;
                margin:0 auto;            
                margin-top:100px;
                   
                ul{
                    line-height: 30px;
                    :nth-child(1){
                        font-weight: bold;
                        font-size:20px;
                    }
                }
            }
        }
    }

    /* ---Skills */
       
    .Skill{
        margin-left:232px;
        background-color:#ecf1f1;;
        height: 900px;
        width: 100%;
        .Skill_information{
            margin:0 auto;
            display:grid;
            grid-template-columns: repeat(3, 400px);
            margin-top:100px;
            .Skill_box{
                width: 280px;
                height: 620px;
                border: 1px solid gray;
                background-color: ghostwhite;
                margin:0 auto;
                text-align: center;
                .text{
                    font-size:22px;
                    padding-top:10px;
                    background-color: black;
                   
                    color:white;
                    padding-bottom:10px;
                }
                svg{
                    font-size:145px;
                    margin:0 auto;
                }
                    
                
                img{
                    width: 250px;
                    height: 200px;
                }
            }
        }
    }

    /* portpolio */

    .portpolio{
        margin-left:232px;
        background-color:#e8edec;
        padding-bottom:120px;
        .portpolio_title{
            margin-top:42px;
            text-align: center ;
            :nth-child(1){
                font-weight: bold;
                font-size:32px;
                margin-bottom:20px;
            }
            :nth-child(2){
                opacity: 0.5;
            }

        }
        .portpolio_information{
            display:flex;
        }
        .portpolio_box{
            margin:100px 0 0 100px;
            
           img{
                width: 400px;
                height: 490px;
                border:1px solid gray;
            }
        }
        .portpolio_explanation{
            margin:100px;
            font-size:18px;
            line-height: 28px;
            span{
                color:red;
            }
            p{
                font-weight: bold;
            }
            hr{
                margin-top:50px;
            }
            .bb{
                margin-top:50px;
                .subtitle{
                    display:flex;
                    margin-bottom: 30px;
                    div:nth-child(1){
                        width: 200px;
                        font-size:24px;
                        font-weight: bold;
                    }
                    div:nth-child(2){
                        font-size:19px;
                    }
                }
            }
            
        }
        
    }
}

@media screen and (max-width:768px){
    main{
        /*  Intro  */
        .Intro{
            margin:0;
            .Intro_div{
               margin:0;
               p{
                   
                    font-size:14px;
               }
               .Intro_header{
                   display:block;
                   font-size:30px;
               }
            
            }
        }
        /*  About  */

        .About{
            width: 100%;
            margin:0;
            .About_information{
                display:grid;
                grid-template-columns: repeat(2,200px);
                .About_informations{
                        width: 150px;
                    ul li{
                        width: 100px;
                        word-wrap: break-word;
                    }
                }
            }
        }
        /* skill */
        .Skill{
            width: 100%;
            margin:0;
            height: auto;
            .title{
                margin-bottom:40px;
            }
            .Skill_information{
                display:block;
                height: auto;;
                margin:0;
               :nth-child(1).Skill_box{
                   margin-bottom:20px;
               }
               :nth-child(2).Skill_box{
                margin-bottom:20px;
            }
            }

        }

        /*potpolio*/
        .portpolio{
            margin:0;
            justify-content: center;
            
            .portpolio_information{
                display:block;

               .portpolio_box{
                    width: 300px;
                    margin:0 auto ;
                img{
                    width: 300px;
                    margin:0 auto;
                }
                
            }
            .portpolio_explanation{
                .bb{
                    .subtitle{
                        display:block;
                    }
                }
            } 
            }
        }
    }
}